import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import BusinessComponent from '../components/BusinessComponent.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../components/About.vue')
    //History mode: https://router.vuejs.org/guide/essentials/history-mode.html#example-server-configurations

    //I think it's just to make the URL look nice lol
  },
  {
    path: '/businesses/:project_id',
    name: 'business',
    component: BusinessComponent,
    props: true
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router